/* Form Container Styling */
.form-container {
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  flex-direction: column;
  background: #f9f9f9; /* Light background */
  padding: 20px; /* Added padding for better spacing */
}

/* Form Styling */
.form-container form {
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1); /* Soft shadow */
  padding: 30px;
  background-color: #fff;
  border-radius: 12px; /* Rounded corners */
  max-width: 500px; /* Limiting form width */
  width: 100%;
}

/* Form Title Styling */
.form-container form .title {
  text-align: center;
  margin-bottom: 30px;
  font-weight: bold;
  font-family: "Playfair Display", serif;
  font-size: 28px; /* Larger font size for title */
  color: #333; /* Darkened title color */
}

/* Input Field Styling */
.form-container form input {
  border: none;
  border-bottom: 1px solid #ccc; /* Lighter border color */
  border-radius: 6px;
  padding: 14px 16px; /* Increased padding */
  margin-bottom: 20px; /* Adjusted margin bottom for better spacing */
  width: 100%;
  font-size: 16px; /* Standard font size for input */
}

/* Button Styling */
.form-container form button {
  border: none;
  border-radius: 25px; /* Rounded button corners */
  background-color: #ff5a5f; /* Trendy button color */
  color: white;
  width: 100%;
  padding: 14px 0; /* Increased padding */
  font-size: 18px; /* Standard font size for button */
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s; /* Smooth transition */
}

.form-container form button:hover {
  background-color: #ff767c; /* Darker button color on hover */
}

/* Forgot Button Styling */
.forgot-btn {
  background-color: transparent;
  border: none;
  color: #333;
  font-size: 14px;
  margin-top: 20px; /* Increased margin top */
  cursor: pointer;
  transition: color 0.3s; /* Smooth transition */
}

.forgot-btn:hover {
  color: #000; /* Darkened text color on hover */
}
