/* Banner Image Styling */
.banner-img {
  margin-top: 60px;
}

/* Home Page Title Styling */
.home-page h1 {
  color: #1a1a1a; /* Dark title color for better visibility */
  font-family: "Roboto", sans-serif; /* Modern sans-serif font */
  font-weight: bold; /* Increased font weight for emphasis */
}

/* Home Page Container Styling */
.home-page {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; /* Center align items */
}

/* Card Styling */
.home-page .card {
  background-color: #f9f9f9; /* Light gray background color for cards */
  width: 18rem;
  border-radius: 10px; /* Rounded corners for cards */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Added subtle shadow for depth */
  margin-bottom: 20px; /* Increased margin bottom for spacing */
}

/* Card Content Styling */
.home-page .card-name-price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px; /* Added padding for better spacing */
}

/* Card Price Styling */
.home-page .card-price {
  color: #ff9900; /* Orange color for price */
  font-weight: bold;
}

/* Button Styling */
.home-page button {
  border: none;
  border-radius: 5px;
  background-color: #ff9900; /* Orange color for button */
  color: #fff; /* White text color for button */
  padding: 10px; /* Increased padding for better appearance */
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease; /* Smooth background color transition on hover */
}

.home-page button:hover {
  background-color: #ffbb33; /* Darker orange color on hover */
}

/* Card Text Styling */
.home-page .card-text {
  color: #666; /* Slightly darkened text color */
  padding: 0 10px; /* Added padding for better spacing */
  margin-bottom: 10px; /* Increased margin bottom for spacing */
}

/* Card Image Styling */
.home-page .card-img-top {
  height: 200px; /* Adjusted image height for consistency */
  object-fit: cover; /* Maintain aspect ratio and cover the container */
  border-top-left-radius: 10px; /* Rounded corners for top-left */
  border-top-right-radius: 10px; /* Rounded corners for top-right */
  transition: transform 0.3s ease; /* Smooth scale transition on hover */
}

.home-page .card-img-top:hover {
  transform: scale(1.05); /* Increased scale on hover for slight zoom effect */
}

/* Filter Section Styling */
.filters h4 {
  margin-top: 100px;
  color: #1a1a1a; /* Dark title color for better visibility */
  border-bottom: 1px solid #ccc; /* Light gray border bottom */
}

/* Checkbox and Radio Button Styling */
.ant-checkbox-wrapper,
.ant-radio-wrapper {
  margin: 5px; /* Adjusted margin for better spacing */
}

/* Button Styling for Filters */
.filters button {
  background-color: #ff9900; /* Orange background color for filters button */
  width: 100%;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  color: #fff; /* White text color for filters button */
  padding: 10px; /* Increased padding for better appearance */
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease; /* Smooth background color transition on hover */
}

.filters button:hover {
  background-color: #ffbb33; /* Darker orange color on hover */
}

/* Load More Button Styling */
.loadmore {
  color: #ff9900; /* Orange color for load more button */
  font-weight: bold;
  font-size: 20px !important;
}
