/* Stylish, Professional, and Attractive Product Details and Similar Products */

/* Product Details Section Styling */
.product-details {
  margin-top: 100px !important;
}

.product-details-info {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center align items horizontally */
  justify-content: center;
  text-align: center; /* Center align text */
}

.product-details-info h6 {
  margin-bottom: 20px !important;
  color: #333; /* Darkened text color for better visibility */
}

/* Similar Products Section Styling */
.similar-products .card {
  background-color: #fff; /* White background color for cards */
  width: 18rem;
  border-radius: 10px; /* Rounded corners for cards */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Added subtle shadow for depth */
  margin-bottom: 20px; /* Increased margin bottom for spacing */
}

/* Card Content Styling */
.similar-products .card-name-price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px; /* Added padding for better spacing */
}

/* Card Price Styling */
.similar-products .card-price {
  color: #4CAF50; /* Green color for price */
  font-weight: bold;
}

/* Button Styling */
.similar-products button {
  border: none;
  border-radius: 5px;
  background-color: #4CAF50; /* Green color for button */
  color: #fff; /* White text color for button */
  padding: 10px; /* Increased padding for better appearance */
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease; /* Smooth background color transition on hover */
}

.similar-products button:hover {
  background-color: #45a049; /* Darker green color on hover */
}

/* Card Text Styling */
.similar-products .card-text {
  color: #666; /* Slightly darkened text color */
  padding: 0 10px; /* Added padding for better spacing */
  margin-bottom: 10px; /* Increased margin bottom for spacing */
}

/* Card Image Styling */
.similar-products .card-img-top {
  height: 200px; /* Adjusted image height for consistency */
  object-fit: cover; /* Maintain aspect ratio and cover the container */
  border-top-left-radius: 10px; /* Rounded corners for top-left */
  border-top-right-radius: 10px; /* Rounded corners for top-right */
  transition: transform 0.3s ease; /* Smooth scale transition on hover */
}

.similar-products .card-img-top:hover {
  transform: scale(1.05); /* Increased scale on hover for slight zoom effect */
}
