/* Main Carousel */
.main-carousel img {
    cursor: pointer;
  }
  
  /* Carousel Container */
  .carousel-container {
    margin-top: 40px;
  }
  
  /* Carousel Item */
  .carousel-item img {
    width: 100%;
  }
  
  /* Carousel Dot */
  .carousel-dot {
    background-color: #ccc;
    width: 10px;
    height: 10px;
    margin: 0 5px;
    border-radius: 50%;
  }
  
  .carousel-dot.active {
    background-color: #ff5722;
  }
  